import { NO_ERRORS_SCHEMA, NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';

import { EmployeeAttendenceRoutingModule } from './employee-attendence-routing.module';
import { EmployeeAttendenceFromComponent } from './employee-attendence-from/employee-attendence-from.component';
import { RouterModule } from '@angular/router';
import { MaterialExampleModule } from 'src/app/material.module';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { BsDatepickerModule } from 'ngx-bootstrap/datepicker';
import { EmployeeAttendenceComponent } from './employee-attendence/employee-attendence.component';
import { FullCalendarModule } from '@fullcalendar/angular';
import dayGridPlugin from '@fullcalendar/daygrid';
import interactionPlugin from '@fullcalendar/interaction';
import listPlugin from '@fullcalendar/list';
FullCalendarModule.registerPlugins([
  dayGridPlugin,
  interactionPlugin,
  listPlugin
]);


@NgModule({
  declarations: [
    EmployeeAttendenceFromComponent,
    EmployeeAttendenceComponent
  ],
  imports: [
    CommonModule,
    EmployeeAttendenceRoutingModule,
    CommonModule,
  
    RouterModule,
    MaterialExampleModule,
    FormsModule,
    ReactiveFormsModule,
    FullCalendarModule,
    BsDatepickerModule.forRoot(),
  ]
})
export class EmployeeAttendenceModule { }
