import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { EmployeeAttendenceFromComponent } from './employee-attendence-from/employee-attendence-from.component';
import { EmployeeAttendenceComponent } from './employee-attendence/employee-attendence.component';

const routes: Routes = [{
  path:'',
  component:EmployeeAttendenceComponent,
  children: [
    {
      path:'',
      redirectTo: 'emp-attendence-form',
      pathMatch: 'full'
    },
    {
      path:'emp-attendence-form',
      component:EmployeeAttendenceFromComponent,
    },
  ],
  pathMatch: 'prefix',
  data: {
    routerLinkActiveOptions: { exact: true, pathMatch: 'full' }
  }
},
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class EmployeeAttendenceRoutingModule { }
