import { Injectable } from '@angular/core';
import { HttpService } from './http.service';
import { UrlConstants } from 'src/app/_helpers/url-constants';

@Injectable({
  providedIn: 'root'
})
export class EmployeeAttendenceService {

  constructor(public httpClient:HttpService) { }
  addAttendenceDetails(data:{}){
    return new Promise((resolve, reject) => {
      this.httpClient.call(data, UrlConstants.postAttendence ,  'POST').subscribe(
        (res) => resolve(res),
        (err) => reject(err)
      );
    });
  }
  getAttendenceDetails(data:{},employeeId){
    return new Promise((resolve, reject) => {
      this.httpClient.call(data, UrlConstants.getAttendence +employeeId ,  'GET').subscribe(
        (res) => resolve(res),
        (err) => reject(err)
      );
    });
  }

}
