import { TitleCasePipe } from '@angular/common';
import { AfterViewInit, ChangeDetectorRef, Component, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, Validators } from '@angular/forms';
import { CalendarOptions } from '@fullcalendar/angular'; // Import CalendarOptions
import { FullCalendarComponent } from '@fullcalendar/angular';
import { EventInput } from '@fullcalendar/core';
import dayGridPlugin from '@fullcalendar/daygrid';
import interactionPlugin from '@fullcalendar/interaction'; //
import { EmployeeAttendenceService } from 'src/app/core/services/employee-attendence.service';
import { RolesPermissionsService } from 'src/app/core/services/roles-permissions.service';
import { ToastNotificationService } from 'src/app/core/services/toast-notification.service';
import { DatePipe } from '@angular/common';
import { ProductService } from 'src/app/core/services/product.service';
import { CommonService } from 'src/app/core/services/common.service';

@Component({
  selector: 'app-employee-attendence-from',
  templateUrl: './employee-attendence-from.component.html',
  styleUrls: ['./employee-attendence-from.component.scss']
})
export class EmployeeAttendenceFromComponent implements OnInit , AfterViewInit {
  @ViewChild('calendar') calendarComponent: FullCalendarComponent; // the #calendar in the template
  calendarVisible = true;
  calendarPlugins = [dayGridPlugin, interactionPlugin];
  calendarWeekends = true;
  calendarEvents: EventInput[] = [
    { title: 'Event Now', start: new Date() }
  ];



  public AttendanceForm: any
  public currentEmployee = JSON.parse(localStorage.getItem("currentUser"));
  public userId: any;
  public AttendeceData: any
  public calDataStatus: any;
  public calDataDate: any;
  public caldata: any;
  public submitted: boolean = false;
  public calenderdata:any;
  public backenddate : Date;
  public today: Date;

  constructor(private formBuilder: FormBuilder, private toastService: ToastNotificationService,private commonService : CommonService,
    private titleCasePipe: TitleCasePipe, public rolesPermissionsService: RolesPermissionsService, public EmployeeAttendenceService: EmployeeAttendenceService, private datePipe: DatePipe,
    private cd : ChangeDetectorRef) 
    {   }

  ngOnInit(): void {
    this.today = new Date();
    console.log("this.today",this.today)
    this.getEmployeeByEmployeeId();
    
    this.getAttendeceForm();
    //this.caldataJSON;

    this.commonService.rowAdded$.subscribe(() => {
      this.getEmployeeAttendenceData();
     });
  }

  ngAfterViewInit(){

    this.getEmployeeAttendenceData();
    this.cd.detectChanges();
  }
  get f() {
    return this.AttendanceForm.controls;
  }
  getAttendeceForm() {
    this.AttendanceForm = this.formBuilder.group({
      start_time: ['', [Validators.required]],
      end_time: ['', [Validators.required]],
      Status: ['', [Validators.required]],
      //uploadDocument: [""],

    });

  }
  toggleVisible() {
    this.calendarVisible = !this.calendarVisible;
  }

  toggleWeekends() {
    this.calendarWeekends = !this.calendarWeekends;
  }

  gotoPast() {
    let calendarApi = this.calendarComponent.getApi();
    calendarApi.gotoDate('2000-01-01'); // call a method on the Calendar object
  }

  handleDateClick(arg) {
    if (confirm('Would you like to add an event to ' + arg.dateStr + ' ?')) {
      this.calendarEvents = this.calendarEvents.concat({ // add new event data. must create new array
        title: 'New Event',
        start: arg.date,
        allDay: arg.allDay
      })
    }
  }
  postAttendence() {
    this.submitted = true;
    if (this.AttendanceForm.invalid) {
      this.toastService.toastMsg({
        title: "Error",
        content: "Fill All Required Fields.",
      });
      // this.toastService.openErrorSnackBar("Please Fill Mandatory Fields!!!");
      return false;
    }
    else {
      console.log(this.AttendanceForm.controls.start_time.value , this.AttendanceForm.controls.end_time.value)
      if (this.AttendanceForm.controls.start_time.value < this.AttendanceForm.controls.end_time.value) {
        let Body = {
          "inTime": this.AttendanceForm.controls.start_time.value,
          "outTime": this.AttendanceForm.controls.end_time.value,
          "status": this.AttendanceForm.controls.Status.value,
          "employeeId": this.currentEmployee.id,
          "userId": this.userId
        }
        this.EmployeeAttendenceService.addAttendenceDetails(Body).then((res: any) => {
          if(res.success){
            this.commonService.notifyDataAdded();
            this.toastService.toastMsg({
              title: "Success",
              content: "Employee Attendence Added Successfully!!!",
            });
            // this.toastService.openSnackBar("Employee Attendence Added Successfully");
            this.AttendanceForm.reset();
            this.submitted = false;
          }
          
        }
          , (err) => {
            if (err.error.expose) {
              this.toastService.toastMsg({
                title: "Error",
                content: this.titleCasePipe.transform(err.error.error_message),
              });
              // this.toastService.openErrorSnackBar(this.titleCasePipe.transform(err.error.error_message));
            }
            else {
              this.toastService.toastMsg({
                title: "Error",
                content: "Something Went Wrong.",
              });
              // this.toastService.openErrorSnackBar("Something Went Wrong.");
            }
          })

      }
      else {
        this.toastService.toastMsg({
          title: "Error",
          content: "In Time Must Be Less Than Out Time!!!",
        });
        // this.toastService.openErrorSnackBar("In time must be less than Out time!!!");
      }

    }

  }
  getEmployeeByEmployeeId() {
    this.rolesPermissionsService.getEmployeeById({}, this.currentEmployee.id).then((res) => {

      //console.log("In Leave --> employee BY UDER Id", res.data);
      this.userId = res.data.parentId;
    }, (err) => {
      if (err.error.expose) {
        this.toastService.toastMsg({
          title: "Error",
          content: this.titleCasePipe.transform(err.error.error_message),
        });
        // this.toastService.openErrorSnackBar(this.titleCasePipe.transform(err.error.error_message));
      }
      else {
        this.toastService.toastMsg({
          title: "Error",
          content: "Something Went Wrong.",
        });
        // this.toastService.openErrorSnackBar("Something Went Wrong.");
      }
    })

  }
  getEmployeeAttendenceData() {
    console.log("enter")
    this.EmployeeAttendenceService.getAttendenceDetails({}, this.currentEmployee.id).then((res: any) => {
      //console.log("Attendece data", res)
      this.AttendeceData = res.data;
      //console.log("Attendece data", this.AttendeceData)
      // this.calDataStatus=this.AttendeceData.map((p)=>{
      //   return p.status
      // })
      // this.calDataDate=this.AttendeceData.map((p)=>{
      //   return p.createdAt
      // })
      if (this.AttendeceData.length > 0) {
        this.backenddate =  this.AttendeceData[this.AttendeceData.length - 1].createdAt;
      } 
      console.log("this.backenddate",this.backenddate)
      this.caldata = this.AttendeceData.map((p) => {
        return {
          title: p.status,
          start: this.datePipe.transform(p.createdAt, 'yyyy-MM-dd'),
        };
      })


      console.log("calData", this.caldata)
      this.calenderdata = JSON.stringify({
        events: this.caldata,
      });


    }, (err) => {
      if (err.error.expose) {
        this.toastService.toastMsg({
          title: "Error",
          content: this.titleCasePipe.transform(err.error.error_message),
        });
        // this.toastService.openErrorSnackBar(this.titleCasePipe.transform(err.error.error_message));
      }
      else {
        this.toastService.toastMsg({
          title: "Error",
          content: "Something Went Wrong.",
        });
        // this.toastService.openErrorSnackBar("Something Went Wrong.");
      }
    })
  }

  // caldataJSON() {
  //   //this.getEmployeeAttendenceData()
  //  // console.log("caldata enter")
  //   return JSON.stringify({
  //     events: this.caldata,
  //   });
  // }

}
